import React, { Component } from "react";
import API from "./components/api";
import InputForm from "./components/inputForm";
import LinkTable from "./components/linkTable";
import Notification from "./components/notification";

export default class App extends Component {
  constructor(props) {
    super(props);

    this.state = {
      links: [],
      showModal: false,
      message: ""
    };
  }

  renderModal = msg => {
    return <Notification message={msg} onClose={this.handleShowModal} />;
  };

  handleShowModal = showModal => {
    this.setState({ showModal });
  };

  getLinks = async () => {
    let res = await API.get("/links", {
      params: {
        orderBy: "createdAt",
        orderDir: "desc",
        limit: "25"
      }
    });
    let links = res.data;
    this.setState({ links });
  };

  createLink = async url => {
    await API.post("/links/", { destination: url }).catch(error => {
      this.setState({ message: error.response.data.message });
      this.handleShowModal(true);
    });
    this.getLinks();
  };

  deleteLink = async id => {
    await API.delete(`/links/${id}`);
    this.getLinks();
  };

  componentDidMount() {
    this.getLinks();
  }

  render() {
    return (
      <React.Fragment>
        {this.state.showModal && this.renderModal(this.state.message)}
        <InputForm onShorten={this.createLink} />
        <LinkTable links={this.state.links} onDelete={this.deleteLink} />
      </React.Fragment>
    );
  }
}

